@import "../../../../styles/variables.scss";

.TopDrawer {
  position: absolute;
  top: 52px;
  left: 0;
  display: none;
  width: 100%;
  height: 30vh;
  background-color: #1b242f;
  color: $secondaryColor;
  border-bottom: 3px solid $secondaryColor;
}

.TopDrawer.Open {
  display: block;
}
