.NavItems {
  margin: 0;
  padding-left: 0;
  width: 35%;
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1250px) {
  .NavItems {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
}
